
@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
::-webkit-scrollbar{
    display: none;
}
a{
    text-decoration: none;
    color: white;
}
ul ,nav ,li{
    list-style: none;
}

body{ 
    font-family: "Poppins", sans-serif;
    overflow: hidden;
}
header{
    padding-left: 100px;
    position: fixed;
    left: 0px;
    top: 0;
    right: 0;
    width: 100%;
    height: 60px;
    display: flex;
    gap: 20px;
    align-items: center;
    z-index: 500;
}
.carousel{
    /* margin-top: -60px; */
    position: relative;
    height: 100vh;
}
.carousel .list .item {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
}
.carousel .list .item  img{
    height: 100%;
    width: 100%;
    object-fit: cover;
    /* position: absolute; */
}
.overlay{
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 20;
}
.carousel .list .item  img.active{
    z-index: 10;
    width: 150px;
    height: 220px;
    position: absolute;
    bottom: 50px;
    left: 50%;
    animation: imageWidth 0.3s 1 forwards linear;
}
@keyframes imageWidth {
    to{
        width: 100%;
        height: 100%;
        left: 0;
        bottom: 0;
        border-radius: 0;
    }

}
.carousel .list .item .content{
    position: absolute;
    left: 22%;
    /* transform: translateX(-50%); */
    top: 17%;
    z-index: 100;
    width: 65%;
    margin: auto;
    color: rgb(219, 218, 218);
    filter: blur(20px);
    transform: translateY(50px);
    opacity: 0;
    transition: 0.5s;
}
.carousel .list .item .content.active{
    z-index: 200;
    display: block;
    filter: blur(0px);
    transform: translateY(0px);
    opacity: 1;
}
.carousel .list .item .content .author{
    color: orange;
    font-size: 25px;
    font-weight: bold;
    /* margin-bottom: 10px; */
}
.carousel .list .item .content .title{
    font-size: 30px;
    font-weight: bold;
    color: rgb(245, 244, 244);
}
.carousel .list .item .content .des{}
.carousel .list .item .content .buttons{
    display: grid;
    grid-template-columns: repeat(2,140px);
    grid-template-rows: 45px;
    gap: 20px;
    margin-top: 20px;
}
.carousel .list .item .content .buttons button{
    cursor: pointer;
    font-size: 18px;
}
.carousel .list .item .content .buttons button:nth-child(2){
    background-color: transparent;
    border: 1px solid #DDD;
    color: white;
}
.carousel .thumbnail{
    position: absolute;
    left: 22%;
    z-index: 20;
    bottom: 30px;
    display: flex;
    gap: 20px;
    justify-content: flex-end;
    align-items: flex-end;
}
html, body {
    overflow-x:hidden 
  }
.carousel .thumbnail .item{
    cursor: pointer;
    width: 150px;
    height: 220px;
    position: relative;
    transition: 0.3s;
}
.carousel .thumbnail .item img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 4px;
}
.thumbnail .content{
    position: absolute;
    bottom: 10px;
    left: 10px;
    color: white;

}
h1 , h2 , h3{
    color: white;
    font-weight: bold;
}
body{
    background-color: #141414fa;
}
.thumbnail img.active{
    filter:brightness(200%)
}

.thumbnail .item.active{
    transform: translateX(calc(-170px));
}

.login-container,.register-container{
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.94);
    z-index: 300;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    visibility: hidden;
    transition: 0.3s;
}
.login-container.show{
    opacity: 1;
    visibility: visible;
}
.register-container.show{
    opacity: 1;
    visibility: visible;
}
.login-container form,.register-container form{
    width: 0;
    transition: 0.4s;
    height: 0;
    padding: 25px;
    background-color: rgba(32, 32, 32, 0.97);
    display: flex;
    flex-direction: column;
    gap: 20px;
    /* border-radius: 8px; */
    overflow: hidden;
    /* box-shadow: 0 0 10px #1a1a1abb; */
}
.login-container.show form,.register-container.show form{
    width: 40%;
    height: auto;
}
.login-container.show button,.register-container.show button{
    width: 50%;
    margin: auto;
    padding: 10px;
    cursor: pointer;
    border: 0;
    background-image:linear-gradient(to bottom left, rgba(0, 0, 0, 0.71) 30%,rgba(29, 29, 29, 0.71),rgba(63, 63, 63, 0.884));
    color: rgb(236, 236, 236);
}
@media (max-width:768px){
    div.login-container.show form,div.register-container.show form{
        width: 95%;
        margin: auto;
    }
}
.login-container form h2,.register-container h2{
    color: rgb(238, 238, 238);
    font-size: 22px;
    font-weight: bold;
}
.login-container form input,.register-container form input{
    color: rgb(236, 236, 236);
    font-size: 17px;
    flex: 1;
    /* border: 1px solid #2c2c2cdd; */
    outline: 0;
    padding: 10px 14px;
    background-color:rgb(12 12 12 / 71%);

}

input::-webkit-input-placeholder{
    color:rgb(56, 56, 56);
}
input:-moz-placeholder {
    color:red;
}

a.active{
    color: orange;
}

.login-container input:focus,.register-container input:focus{
    outline: 1px solid #333333;
}

.see-more{
    position: fixed; 
    overflow: scroll;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(12, 12, 12, 0.97);
    z-index: 301;
    /* display: grid; */
    /* grid-template-columns: repeat(auto-fill,minmax(220px,1fr)); */
    /* grid-template-rows: 360px; */
    /* padding: 80px; */
    padding-top: 100px;
    /* gap: 20px; */
    opacity: 0;
    visibility: hidden;
    transition: 0.3s;
}
.see-more.show{
    opacity: 1;
    visibility: visible;
}
.see-more-item{
    border: 1px solid #292929;
    /* height: auto; */
}
.see-more-info{
    padding: 10px;
    color: rgb(235, 235, 235);
}
.see-more-info h2{
    font-weight: bold;
    font-size: 22px;
    height: 60px;
}
.see-more-item button{
    background-color: #292929;
    color: rgb(228, 228, 228);
}
.see-more-info p{
    /* font-weight: bold; */
    /* font-size: 22px; */
    color: #777777;
    height: 80px;
}
.see-more img{
    width: 100%;
    object-fit: cover;
    height: 150px;
}

.Arrivals{
    display: grid;
    grid-template-columns: repeat(auto-fill,minmax(220px,1fr));
    gap: 20px;
}

.Arrivals .item{
    position: relative;
    /* border: 1px solid; */
    border: 1px solid #222222;
}

.Arrivals .item div p{
    color: rgb(153, 153, 153);

}
.Arrivals .item button{
    background-color: #222222;
    background-image:linear-gradient(to bottom left, rgba(0, 0, 0, 0.71) 30%,rgba(29, 29, 29, 0.71),rgba(63, 63, 63, 0.884));

    cursor: pointer;
    color: #a7a7a7;
}
.Arrivals .item .icon{
    position: absolute;
    right: 10px;
    top: 10px;
    cursor: pointer;
    font-size: 22px;
    color: rgb(12, 120, 243);
}