#resedances div.item {
    padding: 0px;
    transition: 0.2s;
    margin: auto;
    overflow: hidden;
    transition: All 0.3s;

}
#resedances div.item div{
    border: 1px solid #232323;
    border-top: 0;
}
#resedances.item:hover {
    box-shadow: 0px 0px 10px #c6c6c6;
    cursor: pointer;
    background-color: #fafafa;
}

#resedances .image {
    overflow: hidden;
    /* border-radius: 10px; */
}

#resedances .image:hover img {
    transform: scale(1.2);

}

#resedances img {
    transition: 0.3s;
    width: 100%;
    height: 200px;
}

.btns {
    display: flex;
    align-items: center;
}

.btns button {
    border: 1px solid #DDD;
    /* border-radius: 5px; */
    padding: 5px;
    cursor: pointer;
    margin: 0 5px;
    width: 50px;
    background-color: rgb(252, 252, 252);
}

.btns button:hover {
    background-color: #DDD;
}

.btns button:nth-child(1) {
    width: 40px;
    padding: 3px;
    background-color: #eee;
}

.btns button:nth-child(1):hover {
    background-color: #DDD;

}